/* PulseButton.css */
pulsebody {
  height: 100%;
  margin: 0;
  padding: 0;
  

}



.universal-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  margin: 0;
  perspective: 1000px;
  backface-visibility: hidden;
  /* background: #f91942; */
  
}

.universal-pulse-button {
  position: relative;
  display: block;
  width: 100px;
  height: 100px;
  font-size: 1.3em;
  font-weight: 300;
  font-family: 'Trebuchet MS', sans-serif;
  text-transform: uppercase;
  text-align: center;
  line-height: 100px;
  letter-spacing: -1px;
  color: white;
  border: none;
  border-radius: 50%;
  background: #f91942;
  cursor: pointer;
  box-shadow: 0 0 0 0 rgb(0, 0, 0);
  animation: universal-pulse 3.5s infinite;
}

@keyframes universal-pulse {
  0% {
      transform: scale(0.9);
  }
  70% {
      transform: scale(1);
      box-shadow: 0 0 0 25px rgba(90, 153, 212, 0);
  }

  100% {
      transform: scale(0.9);
      box-shadow: 0 0 0 0 rgba(90, 153, 212, 0);
  }
}
