
.title {
  text-align: center;
  margin-bottom: 20px;
  color: black;
}

.WealthJourney{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 10vh;
  margin: 0;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.but {
  padding: 20px;
  max-width: 800px;
  width: 50%;
  text-align: center;
}

.wealth-journey {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: start;
  padding: 20px;
  max-width: 800px;
  width: 100%;
}
.step {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
  margin: 10px;
  text-align: center;
  color: rgb(0, 0, 0);
  background-color: #e71e43;
  height: 120px;
  border-radius: 10%;
  

}


@media (max-width: 600px) {
  .wealth-journey {
    flex-direction: column;
    align-items: center;
  }
}