body {
    font-family: 'Raleway', sans-serif;
    background-color:#fff;
  }
  
  .about-content {
    max-width:800px;
    margin: 0 auto;
  }
  .about-content h2 {
    font-size: 30px;
    line-height:45px;
    margin-bottom: 30px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: #333;
    text-transform: uppercase;
  }
  
  .about-content p {
    font-weight: 300;
    color: #777;
    font-size: 18px;
  }
  
  .about-content {
    padding-right:60px;
  }
  
  .btn-black {
    color:#fff;
    background-color:rgba(0,0,0,1);
    transition: 0.5s;
    border-radius:1;
  }
  
  .btn-black:hover {
    color:#fff;
    background-color:rgba(0,0,0,0.8);
    transition: 0.5s;
  }
  
  @media (max-width:576px) {
    .about-content {
      padding-right:0;
    }
    .about-content h2 {
      font-size:26px;
    }
    .about-content p {
      font-size:16px;
    }
  }