body {
    background: #bfc9ca;
    color: #fff;
}

#main {
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.8), transparent),
                url('https://imageio.forbes.com/blogs-images/ofx/files/2018/09/OFX3-iStock-492595743-1200x800.jpg?format=jpg&height=900&width=1600&fit=bounds');
    background-size: cover;
    background-position: center;
    height: 95vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    position: relative;
}

.welcome-content {
    text-align: right;
    padding: 1rem 5rem 1rem 0;
    position: absolute;
    right: 5%;
    width: 35%;
    background: rgba(0, 0, 0, 0.65);
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.5); /* Added shadow for depth */
}

.welcome-title, .welcome-motto {
    margin: 0;
    color: #fff;
    text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.7);
}

.welcome-title {
    font-size: 2.5rem;
    font-weight: 500; /* Slightly bolder for emphasis */
}

.welcome-motto {
    font-size: 1.5rem; /* Larger for better readability */
}

@media (max-width: 768px) {
    .welcome-content {
        width: 90%; /* Adjusted for better mobile experience */
        padding: 2rem; /* More padding for better touch interaction */
        right: 5%; /* Centered positioning */
        background: rgba(0, 0, 0, 0.85); /* Darker for better legibility */
    }

    .welcome-title {
        font-size: 1.8rem;
    }

    .welcome-motto {
        font-size: 1.2rem;
    }
}
